import api from '../../common/request.js'	

export default {
	data() {
		return {
			typeId: '',
			scrollHeight: 0,
			booklist: [],
			page: 1,
			rows: 8,
			count: '',
			loading: true
		}
	},
	onLoad(option) {
		this.typeId = option.id
		uni.setNavigationBarTitle({
		  title: option.title
		})
		this.getBookListData()
	},
	onReady() {
		const windowHeight = wx.getSystemInfoSync().windowHeight * 2
		this.scrollHeight = windowHeight
	},
	methods: {
		// 字数转换
		wordsCount(num) {
			if (num > 10000) {
			  return Math.round(num / 10000) + '万字'
			} else {
			  return num + '字'
			}
		},
		scrollMoreList() {
		  let page = this.page
		  let length = this.booklist.length
		  if (length >= this.count) {
				this.loading = false
		  } else {
		    page++
		    this.page = page
		    this.getBookListData()
		  }
		},
		getBookListData() {
		  let para = {
		    cId: this.typeId,
		    page: this.page,
		    rows: this.rows
		  }
		  api.get('/book_category/getCategoryBook', para).then(res => {
		    if (res.code === 200) {
		      let arr = this.booklist
		      arr = arr.concat(res.data)
		      if (res.extra.count < 8) {
						this.loading = false
		      }
		      this.booklist = arr
		      this.count = res.extra.count
		    }
		  })
		}
		
	}
	
}	
